import Vue from "vue";
import VueRouter from "vue-router";
import VueCookies from 'vue-cookies';
import store from '@/store/index.js' //
import NProgress from 'nprogress'; //
import 'nprogress/nprogress.css'; //css

// 
import Lgoin from "@/components/Pages/Login/Login.vue" //
import {
    getUser,
} from '@/plugins/api/LoginApi/LoginApi.js'; //LoginApi
import MainIndex from "@/components/MainIndex.vue" //
import Banner from "@/components/Pages/Banner/Banner.vue" //
import TodoListIndex from "@/components/Pages/TodoList/TodoListIndex.vue" //TodoList 

// vue  vueRouter
Vue.use(VueRouter);
Vue.use(VueCookies);

// main.js 
// App.vue  <router-view></router-view>
//  <router-link to="/home">Home</router-link>


const router = new VueRouter ({
    mode: 'history', //history
    routes: [   //
        {
            path: "/",
            meta: {
                requireAuth: true // 
            },
            redirect: "/login" //
        },
        {
            path: "/login", //
            name: "login",
            meta: {
                requireAuth: true // 
                ,index: 0 //   
            },
            component: Lgoin
        },
        {
            path: "/main", //
            name: "main",
            meta: {
                index: 1 //   
            },
            component: MainIndex,
            children: [
                {path:'/',redirect:'/main/banner'}, //
                {path: "todolist",name: "todolist",component: TodoListIndex}, //TodoList
                {path: "banner",name: "banner",component: Banner}, //
            ]
                
        },
        {
            path: "*", //404
            name: "404",
            meta:{
                requireAuth: true // 
            },
            component: () => import("@/components/Pages/404/404.vue")
        }
        
    ]
})

//
router.beforeEach((to, from, next) => {
    NProgress.start(); //

    let refresh_token=""; //refresh_token
    if(VueCookies.get("userInfo") != null && VueCookies.get("userInfo").refresh_token != ""){
        refresh_token = VueCookies.get("userInfo").refresh_token; //Cookie 
    }
    //console.log(store) //store
    //console.log(store.getters.getToken) //store.state.token
    if (!to.meta.requireAuth) {  // 
        if (refresh_token != "" && refresh_token != null) {  // token
            if(store.getters.getUserData.userAccount == "" || store.getters.getUserData.userAccount == null){ //
                getUser(VueCookies.get("userInfo").access_token) //
                .then((response)=>{
                    //
                    if(response.code == 200){
                    store.dispatch("setUserData",response.data) //store
                    console.log(response)
                    next(); //
                    }else{
                    next({
                        path: '/',
                        query: {redirect: to.fullPath}  // path
                    })
                    }
                })
                .catch(err => {
                    //
                    // reject(err)
                    next({
                    path: '/',
                    query: {redirect: to.fullPath}  // path
                    })
                })
            }else {
                next();
            }
        } else {
            next({
                path: '/',
                query: {redirect: to.fullPath}  // path
            })
        }
    }else {
        next();
    }
})

router.afterEach((to, from) => {
    NProgress.done(); //
})

export default router;