<template>
    <div id="Login">
        <div class="container-scroller" id="aplogn">
            <div class="container-fluid page-body-wrapper full-page-wrapper">
                <div class="content-wrapper d-flex align-items-center auth">
                    <div class="row w-100">
                        <div class="col-lg-4 mx-auto">
                            <!-- Main -->
                            <div class="auth-form-light text-left p-5">
                                <div class="brand-logo display-4">  
                                    {{ tTitle }}
                                </div>
                                <h6 class="font-weight-light"></h6>
                                
                                <!--  -->
                                <form class="pt-3 userloginf" v-show="loginShow">
                                    <div class="form-group">
                                        <input
                                            type="text"
                                            class="form-control form-control-lg oBorder"
                                            id="mUserName"
                                            v-model="mUserName"
                                            :placeholder="userName_Pla"
                                            :disabled="!submitL">
                                    </div>
                                    <div class="form-group">
                                        <input 
                                            type="password"
                                            class="form-control form-control-lg oBorder"
                                            id="mPassWord"
                                            v-model="mPassWord"
                                            :placeholder="passWord_Pla"
                                            :disabled="!submitL">
                                    </div>

                                    <div class="mt-3">
                                    
                                        <button
                                            type="button"
                                            :class="submitL ? 'btn btn-block btn-gradient-primary btn-lg auth-form-btn buttonBorder' : 'btn btn-block btn-gradient-primary btn-lg auth-form-btn buttonBorder buttonLoading'"
                                            href="javascript:;"
                                            @click="submitLogin($event)">
                                             </button>
                                    
                                    </div>
                                <div class="my-2 d-flex justify-content-between align-items-center">
                                    <div class="form-check">
                                        <!-- <a href="javascript:;" class="auth-link text-black text-primary" @click="showLogin()"></a> -->
                                    </div>
                                    </div>
                                </form>
                                <!-- End -->
                                
                                <!--  -->
                                <form class="pt-3 smsloginf" name="loginf" method="post" action="" v-show="smsShow">
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input 
                                                type="text" 
                                                name="tphone" 
                                                class="form-control form-control-lg oBorder" 
                                                id="tphone" 
                                                v-model="tphone" 
                                                @input="showYzm()" 
                                                placeholder=""
                                                :disabled="!submitL">
                                        </div>
                                    
                                    </div>
                                    <div class="form-group" v-show="yzmShow">
                                        <input 
                                            type="text" 
                                            name="tyzm" 
                                            class="form-control form-control-lg oBorder" 
                                            id="tyzm" 
                                            v-model="yzm" 
                                            placeholder=""
                                            :disabled="submitL">
                                    </div>
                                    <div class="mt-3">
                                        <button
                                            type="button"
                                            :class="submitLsms ? 'btn btn-block btn-gradient-primary btn-lg auth-form-btn buttonBorder' : 'btn btn-block btn-gradient-primary btn-lg auth-form-btn buttonBorder buttonLoading'"
                                            href="javascript:;"
                                            @click="submitLogin($event)"> </button>
                                    </div>
                                    <div class="my-2 d-flex justify-content-between align-items-center">
                                        <div class="form-check">
                                            <a href="javascript:;" class="auth-link text-black userlogin text-primary" @click="showLogin()"></a>
                                        </div>
                                    </div>
                                    
                                </form>
                                <!-- End -->
                            </div>
                            <!-- Main End-->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    getToken,
    getUser,
    getSussce,
} from '@/plugins/api/LoginApi/LoginApi.js'; //LoginApi

export default {
    name: 'Login',
    data () {
        return {
            tTitle: 'Vue-Test' //

            //
            ,mUserName : '' //
            ,userName_Pla : '' //
            ,mPassWord : '' //
            ,passWord_Pla : '' //
            
            ,submitL : true //
            
            ,loginShow: true //
            ,smsShow: false //
            ,yzmShow: false //
            
            
            //
            ,tphone:"" //
            ,yzm:"" //
            ,submitLsms : true //sms
        }
    },
    mounted() {
        this.isCookie()
    },
    methods: {
        isCookie(){
            if(this.$cookies.get("userInfo") != null && this.$cookies.get("userInfo").access_token != ""){
                this.$router.push({path:"/main/banner"}) //cookie
            }
        }
        ,submitLogin(e) {
            //
            
            var _this = this
            _this.$message.destroy() //
            if(_this.mUserName == ""){
               _this.$message.warning('')
                return false
            }
            if(_this.mPassWord == ""){
                _this.$message.warning('')
                return false
            }
            if(_this.submitL){
                // 
                _this.loadingLogin(e, true) //
                if(_this.loginShow){
                    //
                    //console.log("")

                    // 
                    let cookiesData= {
                        "refresh_token" : "123"
                        ,"access_token" : "123"
                        ,"username" : _this.mUserName
                        ,"userAccount" : "233"
                    }
                    this.$cookies.set("userInfo",cookiesData) //cookie store
                    //this.$store.dispatch("setToken", cookiesData.access_token); //store
                    _this.$store.dispatch("setUserData",cookiesData) //
                    
                    setTimeout(() =>{
                        _this.$message.success('')
                        _this.loadingLogin(e, false) //
                        _this.$router.push({path: '/main/banner'}) // 
                    }, 2000)

                    // 
                    // getToken(_this.mUserName, _this.mPassWord) //
                    // .then((response)=>{
                    //     //
                    //     //console.log(response)
                    //     let cookiesData= {
                    //         "refresh_token" : response.refresh_token
                    //         ,"access_token" : response.access_token
                    //         ,"username" : _this.mUserName
                    //     }
                    //     getUser(cookiesData.access_token) //
                    //     .then((response)=>{
                    //         //
                    //         if(response.code == 200){
                    //             _this.$store.dispatch("setUserData",response.data)    
                    //             _this.$cookies.set("userInfo",cookiesData) //cookie store
                    //             //getSussce(this.$cookies.get("userInfo").access_token)
                    //             _this.$message.success('')
                    //             //console.log(getUser().id) //
                    //             //_this.loadingLogin(e, false) //
                    //             setTimeout(() =>{
                    //                 _this.$router.push({path: '/main/banner'}) //
                    //             },1000)
                    //         }else{
                    //             _this.$router.push({name: 'login'}) // 
                    //             _this.loadingLogin(e, false) // 
                    //         }
                            
        
                    //     })
                    //     .catch(err => {
                    //         //
                    //         // reject(err)
                    //         _this.$message.error(' '+err)
                    //         _this.$router.push({name: 'login'}) // 
                    //         _this.loadingLogin(e, false) // 
                    //     })
    
                    // })
                    // .catch(err => {
                    //     //
                    //     // reject(err)
                    //     _this.$message.error(' '+err)    
                    //     _this.loadingLogin(e, false) // 
                    // })
                    
                }else {
                    //
                    //console.log("")
                    getToken(_this.mUserName, _this.mPassWord)
                    .then((response)=>{
                        //console.log(response)
                        //Token cookies
                        this.$cookies.set("access_token",response.access_token)
                        _this.$message.success('')
                        _this.loadingLogin(e, false) //
                    })
                    .catch(err => {
                        // reject(err)
                        _this.$message.error(' '+err)
                        _this.loadingLogin(e, false) //
                    })
                }
                
            }
            
            
                
        },
        loadingLogin(e, c){
            /**
             * loading 
             * @param   e 
             * @param   c true: false:
             */
            var _this = this
            if(_this.loginShow){
                if(c && _this.submitL){
                    _this.submitL = false; //loading
                    let loadingT = '<i class="anticon anticon-loading"><svg viewBox="0 0 1024 1024" data-icon="loading" width="1em" height="1em" fill="currentColor" aria-hidden="true" class="anticon-spin"><path d="M988 548c-19.9 0-36-16.1-36-36 0-59.4-11.6-117-34.6-171.3a440.45 440.45 0 0 0-94.3-139.9 437.71 437.71 0 0 0-139.9-94.3C629 83.6 571.4 72 512 72c-19.9 0-36-16.1-36-36s16.1-36 36-36c69.1 0 136.2 13.5 199.3 40.3C772.3 66 827 103 874 150c47 47 83.9 101.8 109.7 162.7 26.7 63.1 40.2 130.2 40.2 199.3.1 19.9-16 36-35.9 36z"></path></svg></i>';
                    e.srcElement.innerHTML = loadingT;
                }else if(!c){
                    _this.submitL = true; //loading
                    e.srcElement.innerHTML = " ";
                }
            }else if(_this.smsShow){
                //
                if(c && _this.submitLsms){
                    _this.submitLsms = false //loading
                    let loadingT = '<i class="anticon anticon-loading"><svg viewBox="0 0 1024 1024" data-icon="loading" width="1em" height="1em" fill="currentColor" aria-hidden="true" class="anticon-spin"><path d="M988 548c-19.9 0-36-16.1-36-36 0-59.4-11.6-117-34.6-171.3a440.45 440.45 0 0 0-94.3-139.9 437.71 437.71 0 0 0-139.9-94.3C629 83.6 571.4 72 512 72c-19.9 0-36-16.1-36-36s16.1-36 36-36c69.1 0 136.2 13.5 199.3 40.3C772.3 66 827 103 874 150c47 47 83.9 101.8 109.7 162.7 26.7 63.1 40.2 130.2 40.2 199.3.1 19.9-16 36-35.9 36z"></path></svg></i>'
                    e.srcElement.innerHTML = loadingT
                }else if(!c){
                    _this.submitLsms = true //loading
                    e.srcElement.innerHTML = " "

                }
            }
        },
        showLogin() {
            //
            this.loginShow = !this.loginShow
            this.smsShow = !this.smsShow
        },
        showYzm() {
            //
            this.yzmShow = true
        }
    }
    
}
</script>

<style scoped>
    /*  */
    @import url("./css/Login.css"); 
</style>
