//Aside
const state = {
    urlList:[ //
        {
            menuId:"0",
            menuName: "", //
            menuUrl: "/main/banner", //URL
            menuIcon: "home", //Icon
            
        },
        {
            menuId:"1",
            menuName: "", //
            menuUrl: "", //URL
            menuIcon: "code", //Icon
            children: [
                {
                    menuId:"1.1",
                    menuName: "TodoList", //
                    menuUrl: "/main/todolist", //URL
                    menuIcon: "ordered-list", //Icon
                }
            ]
        },
    ], 
    collapsed: false, //
}
const mutations = {
    set_UrlList(state,urlList) {
        //
        state.urlList = urlList;
    },
    set_Collapsed(state,collapsed) {
        //
        state.collapsed = collapsed;
    },

} 


const actions = {
    setUrlList ({commit},urlList) {
        //
        commit("set_UrlList", urlList);
    },
    setCollapsed ({commit},collapsed) {
        //
        commit("set_Collapsed", collapsed);
    },
}
const getters = {
    getUrlList(state){
        return state.urlList;
    },
    getCollapsed(state){
        return state.collapsed;
    },

}

//
export default {
    state,
    mutations,
    actions,
    getters
}