<template>
    <div id="TodoList">
        <!--  -->
        <el-input type="text" v-model="inputText">
            <el-badge slot="prefix" class="mark" :value="dataList.length" />
            <el-tooltip class="item" slot="append" effect="dark" content=" TodoList" placement="top-end">
                <el-button type="primary" @click="buttonSubmit()">
                    
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" slot="append" effect="dark" content="" placement="top-end">
                <el-button type="primary" @click="handleReturn()">
                    
                </el-button>
            </el-tooltip>
        </el-input>
        <!-- List -->
        
        <el-table
            class="list-table"
            :data="dataList">
            <el-table-column
                label=""
                width="200px">
                <template slot-scope="dataList">
                    <el-tag size="medium">
                        <i class="el-icon-time"></i>
                        {{ dataList.row.todoTime }}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column
                label=""
                width="auto">
                <template slot-scope="dataList">
                    {{ dataList.row.todoText }}
                </template>
            </el-table-column>
            <el-table-column 
                fixed="right"
                label=""
                width="160px">
                <template slot-scope="dataList">
                    <el-button
                        size="mini"
                        @click="handleEdit(dataList.$index, dataList.row)"></el-button>
                    <!--  -->
                    <el-popover
                        placement="top"
                        width="auto"
                        v-model="dataList.showDel">
                        <p></p>
                        <div style="text-align: right; margin: 0">
                            <el-button 
                                type="danger" 
                                size="mini" 
                                @click="handleDelete(dataList.$index, dataList.row)"
                                ></el-button>
                        </div>
                        <el-button class="delSty" type="danger" size="mini" slot="reference"></el-button>
                    </el-popover>
                </template>
            </el-table-column>

        </el-table>
        
        
    </div>
</template>

<script>
export default {
    name: 'TodoList',
    data () {
        return {
            inputText: '', //
            dataList: this.$store.getters.getDataList, //todolist
            oldList:{}, //List
            oldListIndex: '', //List
        }
    },
    methods: {
        buttonSubmit () {
            //
            var _this = this
            let dateTime = _this.Common.getNowTime()//
            if(_this.inputText == ""){
                return false
            }
            _this.oldList = "" //
            _this.oldListIndex = ""
            //
            _this.$store.dispatch('setDataList',{
                todoText: _this.inputText,
                todoTime: dateTime,
                showDel: false, //
            })

            _this.inputText = "" //
        },
        handleEdit(index,row) {
            //
            const _this = this
            console.log(index,row)
            this.$prompt('', ' [ '+row.todoTime+' ]', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /[\S]/,
                inputValue: row.todoText,
                inputErrorMessage: ''
            }).then(({ value }) => {
                //
                // let dataListArray={
                //     dataListIndex: _this.dataList[index],
                //     dataListValue: value
                // }
                //_this.$store.dispatch('setUpdatedata', dataListArray)
                this.$set(this.dataList[index], 'todoText', value)
                this.$message({
                    type: 'success',
                    message: ''
                })
            }).catch(() => {
                //
                // this.$message({
                //     type: 'info',
                //     message: ''
                // });       
            })
        },
        handleDelete(index,row) {
            //
            this.oldList = row //
            this.oldListIndex = index //
            this.dataList.splice(index,1) //
            this.openVn('','success') //

        },
        handleReturn() {
            //
            var _this = this
            if(_this.oldListIndex != ""){
                _this.dataList.splice(this.oldListIndex,0,this.oldList) //
                _this.oldList = "" //
                _this.oldListIndex = ""
                _this.openVn('','success') //
            }else if(_this.oldListIndex == '0'){
                
                _this.dataList.splice(-1,0,this.oldList)//
                _this.oldList = "" //
                _this.oldListIndex = ""
                _this.openVn('','success') //
            }
            
        },
        openVn(nr,setType) {
            //
            this.$message({
                type: setType,
                center: true,
                dangerouslyUseHTMLString: true,
                message: nr
            })
        }
    }
}
</script>

<style scoped>
    .list-table{
        width: 100%;
        margin-top: 24px;
    }
    .delSty{
        margin-left: 4px;
    }
    .mark{
        line-height: 40px;
        margin-top: 4px;
        background-color: transparent !important;
    }
</style>
