import Vue from 'vue'
import App from '@/App.vue'
import axios from 'axios' //axios
import VueCookies from 'vue-cookies'

import '@/lib/PurpleAdmin/css/style.css' //PurpleAdmin

import Antd from 'ant-design-vue/dist/antd.js' //ant-design
import 'ant-design-vue/dist/antd.less' //ant-design

import Common from '@/components/Common/Common.js' //
import router from "@/router/index.js" //
import store from '@/store/index.js' //

axios.defaults.withCredentials = false
Vue.config.productionTip = false

Vue.use(VueCookies); //VueCookies
Vue.use(Antd); //ant-design


Vue.prototype.Common = Common; //

//axios
import {getUserinfo,post,fetch,patch,put} from "@/plugins/http/axios/axios.js";
Vue.prototype.$axios=axios;
Vue.prototype.$getUserinfo=getUserinfo;
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
Vue.prototype.$patch=patch;
Vue.prototype.$put=put;


new Vue({
  router, //
  store, //
  render: h => h(App),
}).$mount('#app')




